.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
body {
  font-family:'nanumsquare';
}
.black-nav {
  background:#000;
  width:100%;
  display:flex;
  color:#fff;
  padding:20px;
  font-weight:600;
  font-size:20px;
}
.list{
  margin-top: 30px;
  text-align:left;
  padding:0 20px; 
}
.modal{
  padding:20px;
  background:#eee;
  margin-top:20px; 
}

.publish{
  margin:30px 0;
}
.publish input{
  padding:10px;
  font-size:16px;
  border-radius: 5px;
  width:80%;
  border:1px solid #ddd;
}
.publish button{
  display: block;
  margin:auto;
  margin-top:10px; 
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
